.title {
    @include circe;
    @include f(40px, 700);
    text-transform: uppercase;
    color: $brown;
    @include lg {
        font-size: 30px; }
    &_lg {
        font-size: 53px;
        letter-spacing: -0.025em; }
    &_m {
        font-size: 30px;
        line-height: 1.5; }
    &_s {
        @include f(22px, 700, 1);
        color: $white; } }

.description {
    @include roboto;
    @include f(20px, 400);
    color: $black; }
